.my-portfolio {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  // gap: 0;
  // row-gap: 0;
  .tile {
    width: 550px;
    height: 300px;
    .container {
      position: relative;
      display: inline-block;
      text-align: center;
      cursor: pointer;

      img {
        background-size: cover;
      }

      &:hover .overlay {
        opacity: 1;
      }

      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        transition: opacity 0.3s ease;
        &-content {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          text-align: center;
          color: white;
        }
      }
    }
  }
}
